.modeling {
  position: relative;

  &__background {
    z-index: -1;
    position: absolute;

    top: 0;
    right: 0;

    width: 340px;
    height: 733px;

    @include vp-1023 {
      top: -284px;
      right: -122px;
    }

    @include vp-767 {
      display: none;
    }

    img {
      width: 340px;
      height: 733px;
    }
  }

  &__wrapper {
    padding: 60px 0;

    @include vp-1023 {
      padding: 50px 0;
    }

    @include vp-767 {
      padding: 40px 0;
    }
  }

  &__title {
    max-width: 892px;
  }

  &__content {
    max-width: 892px;

    font-weight: 400;
    font-size: 16px;
    line-height: 25px;

    color: $color-default-black;

    @include vp-767 {
      font-size: 18px;
      line-height: 27px;
    }

    p {
      margin: 0 0 20px;

      &:last-child {
        margin: 0;
      }
    }

    ul {
      padding-left: 25px;
    }
  }
}
