.custom-checkbox {
  position: relative;
}
.custom-checkbox label {
  display: inline-flex;
  align-items: center;
  cursor: pointer;
}
.custom-checkbox__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  width: 20px;
  height: 20px;
  margin-right: 8px;
  border-radius: 2px;
  background-color: #ffffff;
  transition: background-color 0.3s ease;
}
.custom-checkbox__icon img {
  flex-shrink: 0;
  width: 14px;
  height: 14px;
  transition: visibility 0.3s ease, opacity 0.3s ease;
  visibility: hidden;
  opacity: 0;
}
.custom-checkbox input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}
.custom-checkbox input:checked + .custom-checkbox__icon img {
  visibility: visible;
  opacity: 1;
}
.custom-checkbox input:checked ~ .custom-checkbox__text {
  opacity: 1;
  visibility: visible;
}
.custom-checkbox__label {
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
  user-select: none;
}
@media (max-width: 767px) {
  .custom-checkbox__label {
    font-size: 22px;
    line-height: 33px;
  }
}
.custom-checkbox__text {
  position: absolute;
  top: 100%;
  left: 0;
  margin: 4px 0 0;
  padding-left: 28px;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: rgba(0, 0, 0, 0.5);
  transition: opacity 0.3s ease, visibility 0.3s ease;
  opacity: 0;
  visibility: hidden;
}

.custom-input label {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
}
.custom-input__label {
  display: block;
  margin: 0 0 4px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}
.custom-input input {
  width: 100%;
  padding: 16px 20px 15px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  border: none;
  border-radius: 12px;
  background-color: #f5f5f5;
  color: #000000;
  outline: none;
}
.custom-input input::placeholder {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: rgba(0, 0, 0, 0.5);
}

.custom-radio label {
  position: relative;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
}
.custom-radio__icon {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  width: 20px;
  height: 20px;
  margin-right: 8px;
  border-radius: 50%;
  background-color: #ffffff;
  transition: border 0.3s ease;
}
.custom-radio__icon::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  transition: visibility 0.3s ease, opacity 0.3s ease;
  transform: translate(-50%, -50%);
  visibility: hidden;
  opacity: 0;
}
.custom-radio input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}
.custom-radio input:checked + .custom-radio__icon::after {
  visibility: visible;
  opacity: 1;
}
.custom-radio__label {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  user-select: none;
}

.custom-select {
  position: relative;
  width: 100%;
  max-width: 300px;
}
.custom-select.is-open .custom-select__icon {
  transform: rotate(180deg);
}
.custom-select.is-open .custom-select__button {
  border-radius: 5px 5px 0 0;
}
.custom-select.is-open .custom-select__list {
  visibility: visible;
  opacity: 1;
}
.custom-select__label {
  display: block;
  margin: 0 0 10px;
  font-weight: 0;
  font-size: 16px;
  line-height: 24px;
}
.custom-select__button {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 5px;
  border-radius: 5px;
  background-color: lightgray;
  cursor: pointer;
  outline: none;
}
.custom-select__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #000000;
}
.custom-select__icon {
  flex-shrink: 0;
  width: 20px;
  height: 10px;
  margin-left: 10px;
  background-color: #000;
  transition: transform 0.3s ease;
  clip-path: polygon(100% 0, 100% 20%, 50% 100%, 0 20%, 0 0, 50% 80%);
}
.custom-select__list {
  z-index: 1;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  max-height: 102px;
  margin: 0;
  padding: 0;
  overflow-y: auto;
  list-style: none;
  background-color: lightgrey;
  transition: visibility 0.3s ease, opacity 0.3s ease;
  transform: translateY(100%);
  visibility: hidden;
  opacity: 0;
}
.custom-select__item {
  padding: 5px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #000000;
  transition: background-color 0.3s ease;
  cursor: pointer;
  outline: none;
}
@media (hover: hover) {
  .custom-select__item:hover:not(.focus-visible) {
    background-color: chartreuse;
  }
}
.custom-select__item.focus-visible:focus {
  background-color: chartreuse;
}
.custom-select select {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: none;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.custom-textarea {
