  .modal__wrapper {
    padding: 60px 44px;
  }
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 80px 35px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 475px;
  padding: 40px;
  border-radius: 10px;
  background-color: #ffffff;
}
@media (max-width: 1023px) {
  .modal__content {
    width: 100%;
  }
}
@media (max-width: 767px) {
  .modal__content {
    padding: 30px 20px;
  }
}
.modal__close-button {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 10px;
  right: 10px;
  padding: 0;
  border: none;
  cursor: pointer;
  user-select: none;
}
@media (max-width: 767px) {
  .modal__close-button {
    top: 5px;
    right: 5px;
  }
}
.modal__close-button svg {
  display: block;
  flex-shrink: 0;
  width: 32px;
  height: 32px;
  color: #00289c;
  transition: color 0.3s ease;
}
@media (hover: hover) {
  .modal__close-button:hover:not(.focus-visible) svg {
    color: #0036d1;
  }
}
.modal__close-button.focus-visible:focus svg {
  color: #0036d1;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: auto;
}
