.completed-projects {
  position: relative;

  &__background {
    z-index: -1;
    position: absolute;

    &--left {
      top: 136px;
      left: 0;

      width: 237px;
      height: 735px;

      @include vp-1023 {
        top: -122px;
      }

      @include vp-767 {
        display: none;
      }

      img {
        width: 237px;
        height: 735px;
      }
    }

    &--right {
      top: 389px;
      right: 0;

      width: 236px;
      height: 733px;

      @include vp-1023 {
        display: none;
      }

      img {
        width: 236px;
        height: 733px;
      }
    }
  }

  @include vp-1023 {
    overflow: hidden;
  }

  &__wrapper {
    padding: 60px 0;

    @include vp-1023 {
      padding: 50px 0;
    }

    @include vp-767 {
      padding: 40px 0;
    }
  }

  &__description {
    max-width: 730px;
    margin: 0 0 40px;

    font-weight: 400;
    font-size: 16px;
    line-height: 22px;

    color: $color-default-black;

    @include vp-1023 {
      margin: 0 0 30px;
    }

    @include vp-767 {
      font-size: 18px;
      line-height: 25px;
    }
  }

  &__slider {
    width: 100%;

    @include vp-1023 {
      overflow: visible;
    }
  }

  &__slides {
    width: 100%;
  }

  &__slide {
    width: 320px;
    margin-right: 40px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__image {
    width: 320px;
    height: 300px;
    overflow: hidden;

    border-radius: 8px;

    img {
      width: 320px;
      height: 300px;

      border-radius: 8px;

      object-fit: cover;
    }
  }

  &__title {
    margin: 20px 0 0;

    font-weight: 400;
    font-size: 16px;
    line-height: 22px;

    color: $color-default-black;
  }

  &__button {
    width: 100%;
    max-width: 320px;
    margin: 40px 0 0;

    @include vp-1023 {
      max-width: unset;
    }

    @include vp-767 {
      margin: 30px 0 0;
    }
  }
}
